<template>
  <div class="tags-view-container">
    <a-tag
      v-for="view in visitedViews"
      :key="view.path"
      closable
      @close="closeTag(view)"
      @click="openView(view)"
    >
      {{ view.meta.title || view.name }}
    </a-tag>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  computed: {
    visitedViews() {
      return this.$store.state.vistedView.visitedViews
    }
  },
  methods: {
    ...mapMutations(['delVisitedView']),
    openView(view) {
      this.$router.push(view.path)
    },
    closeTag(view) {
      this.delVisitedView(view)
      // 如果关闭的是当前激活的页面，需要切回一个剩余标签对应的页面
      if (this.$route.path === view.path) {
        const latestView = this.visitedViews.slice(-1)[0]
        if (latestView) {
          this.$router.push(latestView.path)
        } else {
          this.$router.push('/') // 如果没有标签了，回到首页
        }
      }
    }
  }
}
</script>

<style>
.tags-view-container {
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
  background: #f0f2f5; /* 可以根据需要调整背景色 */
}

/* 全局主题色为 #1890ff (AntD默认主题蓝)，可根据需要修改 */
.tags-view-container .ant-tag {
  margin: 5px;
  height: 30px;
  line-height: 28px;
  background-color: #50799e; 
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color .3s ease;
}

  /* 关闭按钮的样式 */
.ant-tag .anticon-close{    
  margin-left: 8px;
  color: #fff !important;
}

.ant-tag :hover {
  background-color: #40a9ff;
}
</style>
